export default "import React from 'react';\r\nimport {Button, ButtonGroup} from 'react-bootstrap';\r\n\r\nconst Sizing = () => {\r\n  return (\r\n    <>\r\n      <ButtonGroup size='lg' className='mb-2'>\r\n        <Button>Left</Button>\r\n        <Button>Middle</Button>\r\n        <Button>Right</Button>\r\n      </ButtonGroup>\r\n      <br />\r\n      <ButtonGroup className='mb-2'>\r\n        <Button>Left</Button>\r\n        <Button>Middle</Button>\r\n        <Button>Right</Button>\r\n      </ButtonGroup>\r\n      <br />\r\n      <ButtonGroup size='sm'>\r\n        <Button>Left</Button>\r\n        <Button>Middle</Button>\r\n        <Button>Right</Button>\r\n      </ButtonGroup>\r\n    </>\r\n  );\r\n};\r\n\r\nexport default Sizing;\r\n";